export default {
	data() {
		return {
			list: [],
			tabCur: 0,
			mainCur: 0,
			verticalNavTop: 0,
			load: true,
			
			currentObject:"",
			allObject:[{
				id:"0",
				name:"家教培训" ,
				content:["学前","小学","初中","高中","大学","艺术","体育","出国留学","生活兴趣","语言培训"]},{
				id:"1",
				name:"考研学长帮" ,
				content:["学前","初中","高中","大学","艺术","体育","出国留学","生活兴趣","语言培训"]},{
				id:"2",
				name:"就业考试咨询",
				content:["学前","初中","高中","大学","艺术","体育","出国留学","生活兴趣","语言培训"]},{
				id:"3",
				name:"家政陪护"    ,
				content:["学前","初中","高中","大学","艺术","体育","出国留学","生活兴趣","语言培训"]},{
				id:"4",
				name:"附近人帮忙"  ,
				content:["学前","初中","高中","大学","艺术","体育","出国留学","生活兴趣","语言培训"]},{
				id:"5",
				name:"临时招工" 	  ,
				content:["学前","初中","高中","大学","艺术","体育","出国留学","生活兴趣","语言培训"]},{
				id:"6",
				name:"接送小孩"	  ,
				content:["学前","初中","高中","大学","艺术","体育","出国留学","生活兴趣","语言培训"]},{
				id:"7",
				name:"情侣匹配"	  ,
				content:["学前","初中","高中","大学","艺术","体育","出国留学","生活兴趣","语言培训"]},{
				id:"8",
				name:"兴趣小组"    ,
				content:["学前","初中","高中","大学","艺术","体育","出国留学","生活兴趣","语言培训"]},{
				id:"9",
				name:"雷达邀请"    ,
				content:["学前","初中","高中","大学","艺术","体育","出国留学","生活兴趣","语言培训"]},{
				id:"10",
				name:"周边服务",
				content:["学前","初中","高中","大学","艺术","体育","出国留学","生活兴趣","语言培训"]}],
			cardCur: 0,

		};
	},
	onLoad:function(options){
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		
		console.log("跳转页面之后获取到的信息",options.item)
		for(let i=0; i < this.allObject.length;i++){
			if (options.item == this.allObject[i].name){
				this.currentObject = this.allObject[i].name;
				this.list = this.allObject[i].content;
				break;
			}
		}
		
	},
	onReady() {
		uni.hideLoading()
	},
	onShow:function(){
		
	},
	
	methods: {
		TabSelect(e) {
			this.tabCur = e.currentTarget.dataset.id;
			this.mainCur = e.currentTarget.dataset.id;
			this.verticalNavTop = (e.currentTarget.dataset.id - 1) * 50
		},
		VerticalMain(e) {



			let that = this;
			let tabHeight = 0;
			if (this.load) {
				for (let i = 0; i < this.list.length; i++) {
					let view = uni.createSelectorQuery().select("#main-" + this.list[i].id);
					view.fields({
						size: true
					}, data => {
						this.list[i].top = tabHeight;
						tabHeight = tabHeight + data.height;
						this.list[i].bottom = tabHeight;
					}).exec();
				}
				this.load = false
			}
			let scrollTop = e.detail.scrollTop + 10;
			for (let i = 0; i < this.list.length; i++) {
				if (scrollTop > this.list[i].top && scrollTop < this.list[i].bottom) {
					this.verticalNavTop = (this.list[i].id - 1) * 50
					this.tabCur = this.list[i].id
					console.log(scrollTop)
					return false
				}
			}
		}
	}
	
}
